@charset "UTF-8";
.fpt-sub-banner {
  position: relative;
}
.fpt-sub-banner .slogan {
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, 50%);
}
@media (max-width: 576px) {
  .fpt-sub-banner .slogan {
    max-width: 12rem;
  }
  .fpt-sub-banner .slogan img {
    max-height: 1rem;
  }
}
.fpt-sub-banner .breadcrumb-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}
.fpt-sub-banner .breadcrumb-wrapper .container {
  background: rgba(242, 111, 33, 0.5);
}
.fpt-sub-banner .breadcrumb-wrapper ol {
  padding: 8px 0 8px 8px;
  list-style: none;
  display: flex;
}
.fpt-sub-banner .breadcrumb-wrapper ol li + li {
  margin-left: 16px;
  padding-left: 16px;
  position: relative;
}
.fpt-sub-banner .breadcrumb-wrapper ol li + li:before {
  position: absolute;
  left: -8px;
  top: 55%;
  transform: translateY(-50%);
  display: inline-block;
  font-family: "Material Design Icons";
  font-size: inherit;
  text-rendering: auto;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: "\f142";
  color: #fff;
  font-size: 18px;
}
.fpt-sub-banner .breadcrumb-wrapper ol li a {
  color: #fff;
  text-decoration: none;
  white-space: nowrap;
  font-family: "Muli";
  font-size: 15px;
}
@media (max-width: 768px) {
  .fpt-sub-banner .breadcrumb-wrapper {
    display: none;
  }
}

.fpt-nav .container {
  background: #fff;
}
.fpt-nav .nav-wrapper {
  padding-top: 72px;
}
@media (max-width: 768px) {
  .fpt-nav .nav-wrapper {
    overflow: hidden;
  }
}
.fpt-nav ul {
  list-style: none;
  display: flex;
  justify-content: center;
  padding: 8px 0;
}
.fpt-nav ul li + li {
  margin-left: 58px;
  position: relative;
}
.fpt-nav ul li + li:before {
  content: "|";
  position: absolute;
  left: -29px;
  top: 50%;
  transform: translateY(-50%);
  color: #999999;
}
.fpt-nav ul li a {
  color: #f26f21;
  text-decoration: none;
  padding: 15px;
  border: 1px solid #f26f21;
  border-radius: 25px;
}
.fpt-nav ul li.active a, .fpt-nav ul li:hover a {
  color: #fff;
  background: #f26f21;
}
@media (max-width: 992px) {
  .fpt-nav ul a {
    font-size: 13px;
  }
}
@media (max-width: 768px) {
  .fpt-nav ul {
    white-space: nowrap;
    display: block;
    overflow-x: auto;
    overflow-y: hidden;
    padding: 15px;
  }
  .fpt-nav ul::-webkit-scrollbar {
    height: 0;
  }
  .fpt-nav ul li {
    display: inline-block;
  }
}
@media (max-width: 768px) {
  .fpt-nav ul {
    text-align: center;
  }
  .fpt-nav ul li a {
    padding: 6px;
    width: 101px;
    display: block;
    white-space: normal;
    text-align: center;
  }
}

.fpt-campus .container {
  background: #fff;
}
.fpt-campus .section-wrapper {
  padding: 30px 0 64px;
}
@media (max-width: 576px) {
  .fpt-campus .section-wrapper {
    padding: 20px 0;
  }
}
.fpt-campus h1 {
  text-align: center;
  color: #f26f21;
  text-transform: uppercase;
  font-size: 35px;
  margin-bottom: 19px;
}
@media (max-width: 576px) {
  .fpt-campus h1 {
    font-size: 17px;
    margin-bottom: 8px;
  }
}
.fpt-campus p {
  color: #333333;
  font-family: "Muli";
  text-align: center;
  max-width: 87%;
  margin: 0 auto;
  margin-bottom: 30px;
}
@media (max-width: 576px) {
  .fpt-campus p {
    max-width: 100%;
    margin-bottom: 15px;
  }
}
.fpt-campus .description {
  position: absolute;
  bottom: 80px;
  z-index: 30;
  width: 100%;
  padding: 0 20px;
}
.fpt-campus .description p {
  max-width: 100%;
  color: #fff;
  text-align: center;
  margin: 0 auto;
}
.fpt-campus .big-slider {
  position: relative;
}
.fpt-campus .big-slider .swiper-slide {
  overflow: hidden;
  position: relative;
}
.fpt-campus .big-slider .swiper-slide img {
  border-radius: 5px;
}
.fpt-campus .big-slider .swiper-prev, .fpt-campus .big-slider .swiper-next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  color: #fff;
  font-size: 50px;
  z-index: 40;
  outline: none;
  cursor: pointer;
}
@media (max-width: 576px) {
  .fpt-campus .big-slider .swiper-prev, .fpt-campus .big-slider .swiper-next {
    display: none;
  }
}
.fpt-campus .big-slider .swiper-next {
  right: 0;
}
@media (max-width: 992px) {
  .fpt-campus .big-slider .swiper-slide .description {
    bottom: 45px;
    font-size: 14px;
  }
}
@media (max-width: 768px) {
  .fpt-campus .big-slider .swiper-slide .description {
    position: static;
    margin-top: 15px;
    font-size: 13px;
  }
  .fpt-campus .big-slider .swiper-slide .description p {
    color: #333;
  }
}
.fpt-campus .small-slider {
  max-width: 83.333333%;
  margin-left: auto;
  margin-right: auto;
  margin-top: -50px;
}
.fpt-campus .small-slider .swiper-slide {
  border: 2px solid white;
  border-radius: 3px;
}
@media (max-width: 992px) {
  .fpt-campus .small-slider {
    margin-top: 30px;
  }
}
@media (max-width: 480px) {
  .fpt-campus .small-slider {
    margin-top: 20px;
  }
}